.blurred {
  filter: blur(6px);
}

.menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.7);
}

.popup {
  position: absolute;
  transform: translate(-50%, -50%);
  background: white;
  /* border-radius: 10px; */
  top: 50%;
  left: 50%;
  width: 300px;
  box-shadow: 0px 0px 43px 14px rgba(156,8,156,1);
  text-align: center;
  padding: 20px;
  box-sizing: border-box;
}

.dashboard {
  position: fixed;
  top: 0;
  right: 0;
  background: white;
  box-shadow: 0px 0px 43px 14px rgba(156,8,156,1);
  padding: 0 20px;
}